*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  font-family: 'Helvetica', 'Arial', sans-serif;
}

.hide {
  visibility: hidden;
}

.wrapper {
  width: 100%;
  max-width: 300px;
  margin: auto;
  text-align: center;
}
